nav {
    min-height: 10vh;
    display: flex;
    background-color: rgb(0, 0, 0);
    color: rgb(65, 95, 160);

    justify-content: space-around;
    align-items: center;
    button {
        background: transparent;
        border: none;
        background-color: rgb(0, 0, 0);
        color: rgb(66, 95, 156);
        font-weight: 400;
        display: flex;
        font-size: 1rem;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        animation: colored 2s infinite alternate linear;
        -webkit-animation: colored 2s infinite alternate linear;
        background-image: linear-gradient(to right, #b0d1f0, #f7e92d);
        // color:
        cursor: pointer;
        padding: 0.5rem;
        transition: all 0.3s ease;
        border: 2px solid rgb(65, 65, 65);
        h4 {
            &:hover {
                animation: none;
                color: white;
            }
        }
        &:hover {
            animation: none;
            background: rgb(65, 65, 65);
            color: white;
        }
    }
}

@keyframes colored {
    from {
        -webkit-filter: hue-rotate(0deg);
    }
    to {
        -webkit-filter: hue-rotate(-360deg);
    }
}

@media screen and (max-width: 768px) {
    nav {
        button {
            z-index: 5;
        }
    }
}
