.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100vh;
    background-color: rgb(26, 25, 25);
    color: rgb(150, 174, 182);
    // border: 1px solid red;
    box-shadow: 2px 2px 10px rgb(24, 24, 24);
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.75s ease;
    h2 {
        padding: 1rem 0;
        text-align: center;
        color: rgb(62, 99, 180);
    }
}
.changed {
    transform: translateX(0);
}
.library-song {
    display: flex;
    align-items: center;
    padding: 1rem 1rem;
    cursor: pointer;
    // border: 1px solid black;
    img {
        width: 30%;
    }
    &:hover {
        background-color: rgb(67, 67, 187);
        color: rgb(254, 255, 255);
    }
    div {
        padding: 0.5rem;
        h3 {
            padding-bottom: 0.25rem;
            padding-left: 0.4rem;
            font-size: 1rem;
        }
        h4 {
            font-size: 0.7rem;
        }
    }
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}
*::-webkit-scrollbar {
    width: 5px;
}
*::-webkit-scrollbar-track {
    background: transparent;
}
*::-webkit-scrollbar-thumb {
    background-color: rgba(155, 155, 155, 0.5);
    border-radius: 20px;
    border: transparent;
}

.selected {
    background-color: rgb(73, 73, 146);
    color: lightcyan;
}

@media screen and (max-width: 768px) {
    .library {
        width: 20rem;
    }
}
