.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
    color: rgb(207, 198, 205);
    width: 100%;
    img {
        width: 25%;
        border-radius: 50%;
    }
    h2 {
        padding: 3rem 1rem 1rem 1rem;
    }
    h3 {
        // padding: 3rem 1rem 1rem 1rem;
        font-size: 1rem;
    }
}

@media screen and (max-width: 768px) {
    .song-container {
        min-height: 60vh;
        padding-top: 1rem;
        img {
            width: 75%;
            border-radius: 20px;
        }
        h2 {
        }
    }
}
