.player-container {
    min-height: 25vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(0, 0, 0);
    color: rgb(62, 99, 180);
}

.time-control {
    width: 50%;
    display: flex;
    // border: 1px solid red;
    align-items: center;
    // justify-content: space-around;
    // background-color: burlywood;
    input {
        width: 100%;

        -webkit-appearance: none;
        background-color: transparent;
        cursor: pointer;
    }
    p {
        padding: 1rem;
        color: rgb(221, 216, 216);
        // border: 1px solid green;
    }
}

.play-control {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    .button-container {
        background: rgb(204, 201, 201);
        padding: 1.1rem;
        z-index: 2;
        color: rgb(3, 6, 12);
        border-radius: 50%;
    }
    svg {
        cursor: pointer;
    }
}

input[type="range"]:focus {
    outline: none;
}
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
    // background-color: black;
}
input[type="range"]::-moz-range-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
}

.track {
    background-color: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}

.animate-track {
    background-color: rgb(49, 49, 49);
    width: 100%;
    height: 105%;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    transform: translateX(10%);
}

@media screen and (max-width: 768px) {
    .time-control {
        width: 90%;
    }
    .play-control {
        width: 60%;
    }
}
